@import "../../global.scss";

.portfolio{
    background-color: white;
    display: flex;
    flex-direction: column;
    align-items: center;

    h1{
        font-size: 50px;
        overflow: hidden;
        @include mobile{
            font-size: 20px;
        }
    }

    ul{
        margin:10px;
        padding: 0;
        list-style: none;
        display: flex;

        @include mobile{
            margin:10px 0px;
            flex-wrap: wrap;
            justify-content: center;
        }

        li{
            overflow: hidden;
            font-size: 16px;
            margin-right: 50px;
            padding: 7px;
            border-radius: 6px;
            cursor:pointer;

            @include mobile{
                padding: 5px 6px;
                font-size: 13.5px;
            }

            &.active{
                background-color: $mainColor;
                color: white;
            }
        }
    }

    .container{
        width: 100%;
        justify-content: center;
        align-items: center;
        // flex-wrap: wrap;

        @include mobile{
            width: 100%;
        }

